<template>
    <div id="app">
        <header class="header-wrapper">
            <i class="logo"></i>
            <nav class="nav-panel">
                <ul class="nav-list left-nav">
                    <router-link tag="li" :to="ln.link" class="nav-items" v-for="(ln, index) in leftNav" :key="index"
                    :class="{'active-item': fstSelected === 0 && sndSelected === index}"
                    @click.native="fstSelected = 0; sndSelected = index; setIndex(0, index)">{{ln.name}}</router-link>
                </ul>
                <ul class="nav-list right-nav">
                    <router-link tag="li" :to="rn.link" class="nav-items" v-for="(rn, index) in rightNav" :key="index"
                    :class="{'active-item': fstSelected === 1 && sndSelected === index}"
                    @click.native="fstSelected = 1; sndSelected = index; setIndex(1, index)">{{rn.name}}</router-link>
                    <router-link tag="li" to="" class="nav-items search-item"></router-link>
                </ul>
            </nav>
        </header>
        <div class="main-wrapper">
            <router-view/>
        </div>
        <footer class="footer-wrapper">
            <div class="footer-panel">
                <div class="foot-left">
                    <div class="contact-us">
                        <span class="cn">联系我们</span>
                        <span class="en">contact us</span>
                    </div>
                    <i class="foot-logo"></i>
                    <div class="foot-info">
                        <span class="foot-title">ES中国总部</span>
                        <span class="foot-content">地址：四川省成都市高新区天府大道中段530号东方希望天祥广场A座30楼</span>
                        <span class="foot-content">电话：028-61333945  028-61333946 18081185693</span>
                    </div>
                    <div class="foot-info">
                        <span class="foot-title">ES英国总部</span>
                        <span class="foot-content">地址：Unit 26 Wellington Business Park, Dukes Ride, Crowthorne, Berkshire, RG45 6LS</span>
                        <span class="foot-content">电话：+44 (0)1344771000</span>
                    </div>
                </div>
                <div class="foot-right">
                    <div class="input-items">
                        <span>姓名</span>
                        <input type="text" v-model="contactInfo.name">
                    </div>
                    <div class="input-items">
                        <span>邮箱</span>
                        <input type="text" v-model="contactInfo.email">
                    </div>
                    <div class="input-items">
                        <span>电话</span>
                        <input type="text" v-model="contactInfo.phone">
                    </div>
                    <div class="input-items content-item">
                        <span>内容</span>
                        <textarea type="text" rows="3" v-model="contactInfo.content"></textarea>
                    </div>
                    <button class="submit-btn">提交</button>
                </div>
            </div>
            <div class="copyright-panel">Copyright © 2018 www.elitescheme.com All Rights Reserved ES.版权所有 蜀ICP备13004617号-1</div>
        </footer>
        <div class="share-wrapper">
            <i class="phone"></i>
            <i class="weibo"></i>
            <i class="weixin"></i>
            <v-back-top></v-back-top>
        </div>
    </div>
</template>

<script>
export default {
    name: "App",
    data(){
        return{
            leftNav: [
                {
                    link: '/',
                    name: '首页'
                },
                {
                    link: '/english-education',
                    name: '英国教育'
                },
                {
                    link: '/school-search',
                    name: '名校查询'
                },
                {
                    link: '/success-case',
                    name: '成功案例'
                },
                {
                    link: '/activity-info',
                    name: '活动资讯'
                }
            ],
            rightNav: [
                {
                    link: '',
                    name: '短期游学'
                },
                {
                    link: '/train',
                    name: '培训辅导'
                },
                {
                    link: '',
                    name: '关于ES'
                },
                {
                    link: '',
                    name: 'VIP定制游'
                },
            ],
            fstSelected: 0,
            sndSelected: 0,
            contactInfo: {
                name: '',
                email: '',
                phone: '',
                content: ''
            }
        }
    },
    mounted(){
        let that = this
        if(sessionStorage.getItem("fstSelected")){
            that.fstSelected = parseInt(sessionStorage.getItem("fstSelected"))
        }
        if(sessionStorage.getItem("sndSelected")){
            that.sndSelected = parseInt(sessionStorage.getItem("sndSelected"))
        }
    },
    methods: {
        setIndex(f, s){
            sessionStorage.setItem("fstSelected", f)
            sessionStorage.setItem("sndSelected", s)
        }
    }
}
</script>

<style lang="scss" type="text/css"></style>



