<template>
    <div class="school-search-wrapper">
        <div class="banner">
            <div class="search-panel">
                <i></i>
                <input type="text" v-model="queryTerm" placeholder="学校名 / 区域 / 位置">
            </div>
        </div>
        <div class="school-search-main">
            <ul class="filter-wrapper">
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.area.title}} :</span>
                    <v-checkbox v-model="filterItems.area.allChecked"
                    @click="checkAll(filterItems.area)">全选</v-checkbox>
                    <v-checkbox-group :data="filterItems.area.items"
                    v-model="filterItems.area.checkItems"
                    @change="setState(filterItems.area)"></v-checkbox-group>
                </li>
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.schoolType.title}} :</span>
                    <v-checkbox v-model="filterItems.schoolType.allChecked"
                    @click="checkAll(filterItems.schoolType)">全选</v-checkbox>
                    <v-checkbox-group :data="filterItems.schoolType.items"
                    v-model="filterItems.schoolType.checkItems"
                    @change="setState(filterItems.schoolType)"></v-checkbox-group>
                </li>
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.studyForm.title}} :</span>
                    <v-checkbox v-model="filterItems.studyForm.allChecked"
                    @click="checkAll(filterItems.studyForm)">全选</v-checkbox>
                    <v-checkbox-group :data="filterItems.studyForm.items"
                    v-model="filterItems.studyForm.checkItems"
                    @change="setState(filterItems.studyForm)"></v-checkbox-group>
                </li>
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.ageGroup.title}} :</span>
                    <v-checkbox v-model="filterItems.ageGroup.allChecked"
                    @click="checkAll(filterItems.ageGroup)">全选</v-checkbox>
                    <v-checkbox-group :data="filterItems.ageGroup.items"
                    v-model="filterItems.ageGroup.checkItems"
                    @change="setState(filterItems.ageGroup)"></v-checkbox-group>
                </li>
                <li class="filter-panel">
                    <span class="items-title">{{filterItems.schoolAge.title}} :</span>
                    <v-select class="age-select" size="sm" style="width: 75px;"
                    :data="filterItems.schoolAge.bOptions"
                    v-model="filterItems.schoolAge.minVal"
                    @change="judgeVal(filterItems.schoolAge)"></v-select>
                    <span class="line">—</span>
                    <v-select size="sm" style="width: 75px;"
                    :data="filterItems.schoolAge.eOptions"
                    v-model="filterItems.schoolAge.maxVal">
                        <template slot-scope="{data}">
                            {{data.label}}
                        </template>
                    </v-select>
                </li>
            </ul>
            <div class="result-wrapper">
                <div class="result-panel">共筛选出：122所学校</div>
                <div class="compare-panel">
                    <span class="title" v-if="compareItems.length > 0">已选择：</span>
                    <span class="compare-item" v-for="ci in compareItems" :key="ci.id">{{ci.cname}}<i class="delete-icon" @click="cancelContrast(ci)"></i></span>
                    <button class="compare-btn">开始对比</button>
                </div>
            </div>
            <div class="school-wrapper">
                <ul class="school-list">
                    <li class="school-item" v-for="sl in schoolList" :key="sl.id">
                        <div class="school-logo"><img :src="sl.logo" alt=""></div>
                        <div class="school-info">
                            <h2 class="school-name" v-tooltip.topleft='sl.cname+ " " +sl.ename'>{{sl.cname}} {{sl.ename}}</h2>
                            <div class="school-tags">
                                <span class="tag" v-for="t in sl.tag" :key="t.id" :class="t.type">{{t.result}}</span>
                            </div>
                            <p>学校特色：{{sl.characteristic}}</p>
                            <button class="contrast-btn" v-if="!sl.isCompare" @click="addContrast(sl)">加入对比</button>
                            <button class="contrast-btn cancel-status" @click="cancelContrast(sl)" v-else>取消对比</button>
                        </div>
                    </li>
                </ul>
                <div class="map-panel"></div>
            </div>
            <div class="pagination-wrapper">
                <v-pagination show-quick-jumper :total="500"></v-pagination>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            queryTerm: '',
            filterItems: {
                area: {
                    title: '热门区域',
                    allChecked: false,
                    items: [
                        {
                            label: '伦敦',
                            value: 'london'
                        },
                        {
                            label: '牛津',
                            value: 'oxford'
                        },
                        {
                            label: '剑桥',
                            value: 'cambridge'
                        },
                        {
                            label: '英格兰南部',
                            value: 'sEngland'
                        },
                        {
                            label: '英格兰中部',
                            value: 'mEngland'
                        },
                        {
                            label: '英格兰北部',
                            value: 'nEngland'
                        }
                    ],
                    checkItems: []
                },
                schoolType: {
                    title: '学校类型',
                    allChecked: false,
                    items: [
                        {
                            label: '男校',
                            value: 'maleSchool'
                        },
                        {
                            label: '女校',
                            value: 'femaleSchool'
                        },
                        {
                            label: '合校',
                            value: 'coed'
                        }
                    ],
                    checkItems: []
                },
                studyForm: {
                    title: '就读形式',
                    allChecked: false,
                    items: [
                        {
                            label: '全寄宿学校',
                            value: '1'
                        },
                        {
                            label: '全走读学校',
                            value: '2'
                        },
                        {
                            label: '寄宿兼走读学校',
                            value: '3'
                        }
                    ],
                    checkItems: []
                },
                ageGroup: {
                    title: '学龄段',
                    allChecked: false,
                    items: [
                        {
                            label: '预备校',
                            value: '1'
                        },
                        {
                            label: '中学',
                            value: '2'
                        },
                        {
                            label: '全年龄段学校',
                            value: '3'
                        }
                    ],
                    checkItems: []
                },
                schoolAge: {
                    title: '入学年龄',
                    bOptions: [
                        {
                            value: 4,
                            label: '4岁'
                        },
                        {
                            value: 5,
                            label: '5岁'
                        },
                        {
                            value: 6,
                            label: '6岁'
                        },
                        {
                            value: 7,
                            label: '7岁'
                        },
                        {
                            value: 8,
                            label: '8岁'
                        },
                        {
                            value: 9,
                            label: '9岁'
                        },
                        {
                            value: 10,
                            label: '10岁'
                        },
                        {
                            value: 11,
                            label: '11岁'
                        },
                        {
                            value: 12,
                            label: '12岁'
                        },
                        {
                            value: 13,
                            label: '13岁'
                        },
                        {
                            value: 14,
                            label: '14岁'
                        },
                        {
                            value: 15,
                            label: '15岁'
                        },
                        {
                            value: 16,
                            label: '16岁'
                        },
                        {
                            value: 17,
                            label: '17岁'
                        },
                        {
                            value: 18,
                            label: '18岁'
                        }
                    ],
                    eOptions: [
                        {
                            value: 4,
                            label: '4岁'
                        },
                        {
                            value: 5,
                            label: '5岁'
                        },
                        {
                            value: 6,
                            label: '6岁'
                        },
                        {
                            value: 7,
                            label: '7岁'
                        },
                        {
                            value: 8,
                            label: '8岁'
                        },
                        {
                            value: 9,
                            label: '9岁'
                        },
                        {
                            value: 10,
                            label: '10岁'
                        },
                        {
                            value: 11,
                            label: '11岁'
                        },
                        {
                            value: 12,
                            label: '12岁'
                        },
                        {
                            value: 13,
                            label: '13岁'
                        },
                        {
                            value: 14,
                            label: '14岁'
                        },
                        {
                            value: 15,
                            label: '15岁'
                        },
                        {
                            value: 16,
                            label: '16岁'
                        },
                        {
                            value: 17,
                            label: '17岁'
                        },
                        {
                            value: 18,
                            label: '18岁'
                        }
                    ],
                    minVal: '',
                    maxVal: ''
                }
            },
            schoolList: [
                {
                    logo: require('../static/school-search/school-logo.png'),
                    cname: '伊顿公学',
                    ename: 'Eton College',
                    tag: [
                        {
                            type: 'studyForm',
                            result: '全寄宿学校'
                        },
                        {
                            type: 'schoolAge',
                            result: '13-18岁'
                        }
                    ],
                    characteristic: '该校地理位置优越，位于一个典型的英国乡村附近，风景秀丽，设施齐全是理想的学习圣地。有着342年历史的英国知名学院。',
                    isCompare: false
                },
                {
                    logo: require('../static/school-search/school-logo.png'),
                    cname: '卡迪克特预备学校',
                    ename: 'Eton College',
                    tag: [
                        {
                            type: 'studyForm',
                            result: '全寄宿学校'
                        },
                        {
                            type: 'schoolAge',
                            result: '13-18岁'
                        }
                    ],
                    characteristic: '该校地理位置优越，位于一个典型的英国乡村附近，风景秀丽，设施齐全是理想的学习圣地。有着342年历史的英国知名学院。',
                    isCompare: false
                },
                {
                    logo: require('../static/school-search/school-logo.png'),
                    cname: '卡迪克特全寄宿预备学校',
                    ename: 'Eton College',
                    tag: [
                        {
                            type: 'studyForm',
                            result: '全寄宿学校'
                        },
                        {
                            type: 'schoolAge',
                            result: '13-18岁'
                        }
                    ],
                    characteristic: '该校地理位置优越，位于一个典型的英国乡村附近，风景秀丽，设施齐全是理想的学习圣地。有着342年历史的英国知名学院。',
                    isCompare: false
                }
            ],
            compareItems: []
        }
    },
    methods: {
        checkAll(obj){
            if(obj.checkItems.length == obj.items.length){
                obj.checkItems = []
                obj.allChecked = false
            }else{
                let temp = JSON.parse(JSON.stringify(obj.items))
                obj.checkItems.splice(0, obj.checkItems.length)
                for(let i = 0; i < temp.length; i++){
                    obj.checkItems.push(temp[i].value)
                }
                obj.allChecked = true
            }
        },
        setState(obj){
            if(obj.checkItems.length == obj.items.length){
                obj.allChecked = true
            }else{
                obj.allChecked = false
            }
        },
        judgeVal(obj){
            obj.maxVal = ''
            let min = obj.minVal
            let arr = obj.eOptions
            let that = this
            for(let i = 0; i < obj.eOptions.length; i++){
                if(obj.eOptions[i].value < min){
                    that.$set(that.filterItems.schoolAge.eOptions[i], 'disabled', true)
                }
            }
        },
        addContrast(o){
            o.isCompare = true
            this.compareItems.push(o)
        },
        cancelContrast(o){
            let arr = this.compareItems
            if(arr.length > 0){
                o.isCompare = false
                for(let j=0; j<arr.length; j++){
                    if(arr[j] == o){
                        arr.splice(j, 1)
                    }
                }
            }
            // console.log(index)
        }
    }
}
</script>

<style>

</style>



