<template>
    <div class="school-detail-wrapper">
        <div class="banner"></div>
        <div class="school-detail-main">
            <div class="breadcrumd-wrapper">
                <span class="l-title">您的位置：</span>
                <v-breadcrumb separator=">">
                    <v-breadcrumb-item>
                        <router-link to="/">首页</router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>
                        <router-link to="/school-search">名校查询</router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>伊顿公学Eton College</v-breadcrumb-item>
                </v-breadcrumb>
            </div>
            <div class="school-panel">
                <div class="school-logo"></div>
                <div class="school-info">
                    <h3 class="school-name">伊顿公学 Eton College</h3>
                    <p class="school-desc">学校特色：该校地理位置优越，位于一个典型的英国乡村附近，风景秀丽，设施齐全是理想的学习圣地。学校特色：该校地理位置优越，位于一个典型的英国乡村附近，风景秀丽，设施齐全是理想的学习圣地。有着342年历史的英国知名学院。</p>
                    <div class="btn-wrapper">
                        <button class="contrast-btn">加入对比</button>
                        <button class="compare-btn">在线申请</button>
                    </div>
                </div>
            </div>
            <div class="school-shows">
                <div class="show-left">
                    <v-carousel
                    v-model="value2"
                    :arrow="'always'"
                    :dots="'none'">
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                        <v-carousel-item>
                            <div class="show-item"></div>
                        </v-carousel-item>
                    </v-carousel>
                    <ul class="school-characteristics">
                        <li v-for="s in scList" :key="s.id">
                            <span class="title">{{s.title}}</span>
                            <span class="result">{{s.result}}</span>
                        </li>
                    </ul>
                </div>
                <div class="show-right"></div>
            </div>
            <div class="school-details-panel">
                <ul class="detail-tabs">
                    <li v-for="(t, index) in tabList" :key="index"
                    :class="{'active':tabSelected == index}"
                    @click="tabSelected = index">{{t.name}}</li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            value2: 0,
            scList: [
                {
                    title: '就读形式',
                    result: '全寄宿学校'
                },
                {
                    title: '院校类型',
                    result: '男校'
                },
                {
                    title: '招生年龄',
                    result: '13-18岁'
                },
                {
                    title: '国际学生比例',
                    result: '8%'
                },
                {
                    title: '学费',
                    result: '500000英镑'
                },
                {
                    title: '最新学术排名',
                    result: '10'
                },
                {
                    title: '地理位置',
                    result: '英格兰南部'
                },
                {
                    title: '建校时间',
                    result: '1440年'
                },
                {
                    title: '学校面积',
                    result: '100英亩'
                },
                {
                    title: '师生比',
                    result: '1:0.5'
                },
                {
                    title: '学生人数',
                    result: '50000人'
                },
                {
                    title: '学校官网',
                    result: '点击访问'
                }
            ],
            tabList: [
                {
                    name: '详情介绍'
                },
                {
                    name: '学术成绩'
                },
                {
                    name: '学科设置'
                },
                {
                    name: '知名校友'
                }
            ],
            tabSelected: 0
        }
    }
}
</script>

<style>

</style>



