<template>
    <div class="activity-info-wrapper">
        <div class="banner"></div>
        <div class="activity-info-detail-main">
            <div class="breadcrumd-wrapper">
                <span class="l-title">您的位置：</span>
                <v-breadcrumb separator=">">
                    <v-breadcrumb-item>
                        <router-link to="/">首页</router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>
                        <router-link to="/school-search">活动资讯</router-link>
                    </v-breadcrumb-item>
                    <v-breadcrumb-item>精彩活动</v-breadcrumb-item>
                    <v-breadcrumb-item>正文</v-breadcrumb-item>
                </v-breadcrumb>
            </div>
            <h1 class="activity-title">E探究竟 | 探访英国中学版“牛津剑桥”</h1>
            <ul class="activity-info">
                <li class="info-items">
                    <span class="item-title">活动日期：</span>
                    <span class="item-content">2019.03.18-2029.03.20</span>
                </li>
                <li class="info-items">
                    <span class="item-title">活动时间：</span>
                    <span class="item-content">09:00-18:00</span>
                </li>
                <li class="info-items">
                    <span class="item-title">活动地点：</span>
                    <span class="item-content">伦敦</span>
                </li>
                <li class="info-items">
                    <span class="item-title">费用：</span>
                    <span class="item-content">800-1000元</span>
                </li>
                <li class="info-items">
                    <span class="item-title">联系电话：</span>
                    <span class="item-content">13605270828</span>
                </li>
            </ul>
            <div class="signup-panel">
                <div class="sign-items" style="width: 13%">
                    <sub>*</sub>
                    <span class="item-title">家长姓名：</span>
                    <v-input size="small" v-model="signupForm.parentName"></v-input>
                </div>
                <div class="sign-items" style="width: 25%">
                    <sub>*</sub>
                    <span class="item-title">家长电话：</span>
                    <v-input size="small" v-model="signupForm.parentNumber.number">
                        <v-select size="small" placeholder="请选择" style="width: 80px" slot="before" :data="codeOptions"
                        v-model="signupForm.parentNumber.areaCode"></v-select>
                    </v-input>
                </div>
                <div class="sign-items" style="width: 24%">
                    <sub>*</sub>
                    <span class="item-title">所在城市：</span>
                    <v-input size="small" v-model="signupForm.city.cityName">
                        <v-select size="small" placeholder="请选择" style="width: 60px" slot="before" :data="provinceOptions"
                        v-model="signupForm.city.province"></v-select>
                    </v-input>
                </div>
                <div class="sign-items" style="width: 15%">
                    <sub>*</sub>
                    <span class="item-title">参营人数：</span>
                    <v-input-number size="small" v-model="signupForm.personNumber"></v-input-number>
                    <span style="margin-left: 10px">人</span>
                </div>
                <button class="signup-btn">我要报名</button>
            </div>
            <div class="activity-content">
                <div class="content-left" v-html="activityContent"></div>
                <div class="content-right">
                    <div class="recommend-panel panel1">
                        <div class="bg-panel"></div>
                        <div class="theme-panel"></div>
                    </div>
                    <div class="recommend-panel panel2">
                        <div class="bg-panel"></div>
                        <div class="theme-panel"></div>
                    </div>
                    <div class="recommend-panel panel3">
                        <div class="bg-panel"></div>
                        <div class="theme-panel"></div>
                    </div>
                    <div class="recommend-panel panel4">
                        <div class="bg-panel"></div>
                        <div class="theme-panel"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            signupForm: {
                parentName: '',
                parentNumber: {
                    areaCode: 'china',
                    number: ''
                },
                city: {
                    province: 'china',
                    cityName: ''
                },
                personNumber: ''
            },
            codeOptions: [
                {
                    label: '中国+86',
                    value: 'china'
                },
                {
                    label: '英国+44',
                    value: 'england'
                }
            ],
            provinceOptions: [
                {
                    label: '中国',
                    value: 'china'
                },
                {
                    label: '江苏',
                    value: 'jiangsu'
                }
            ],
            activityContent: `
            <div style="font-size: 13px; color: #333; line-height: 1.8;">
                    上周，ES带领着来自北京的8岁男孩Kevin，探访了“英国公学双雄”——伊顿公学和哈罗公学。
                    英国社会，似乎很喜欢制造“既生瑜，何生亮”的局面。比如，有了保守党，就得有个工党；有了曼联，就得有个利物浦；有了牛津，就得有个剑桥。
                而牛津剑桥这对“老对手”，在英国的中学里也有“翻版”，那就是伊顿公学（Eton College）和哈罗公学（Harrow School）。

                ▲ 哈罗公学教堂（左）和伊顿公学主教学楼（右）
                伊顿公学建于1441年（明朝正统六年），在公学历史上坐实老二。老大是14世纪时创立的王思聪和霍启刚的母校，温切斯特公学（Winchester College）。
                100多年后的1572年（明朝隆庆六年），哈罗公学诞生。随后在几百年的改革变化中，伊顿哈罗摇身变成了亦敌亦友的“公学双雄”，在贵族男校领域都把对方看做是值得超越的对手。
                ▲ 伊顿教堂内部（左）和哈罗教堂内部（右）
                跟牛津和剑桥一样，伊顿和哈罗的“相爱相杀”，也会在体育竞技中体现。牛剑有年度的划船比赛，伊顿哈罗则有年度的板球比赛。
                伊顿哈罗板球赛是全球最早的到现在仍在举行的年度体育赛事之一，开始于1805年，比牛剑开始于1829年的划船比赛还早。当年，著名诗人拜伦作为哈罗学生，参加了第一场的伊顿哈罗板球赛。
                200多年来，两所英国最顶尖的贵族男校都“打过不少败仗”，但总体来说，他们还是实力均衡，胜负平均。
                ▲ 哈罗学长带8岁的Kevin参观哈罗
                2015年的奥斯卡颁奖典礼，伊顿毕业的“小雀斑”埃迪·雷德梅恩和哈罗毕业的“卷福”本尼迪克特·康伯巴奇共同提名最佳男主角，也被大家看作是伊顿和哈罗“暗地里的较量”。
                当然，最后是代表伊顿的小雀斑，捧回了小金人。
                伊顿还经常搬出共培育了19位英国首相、2位英国王子的事实来“怼”培养过9位英国首相的哈罗。然而，这两所公学都是以培养政治军事人才为目标的学校，各自的校友也都不乏君主、贵族及名人，比如伊顿的泰国前总理、比利时国王、诗人雪莱；哈罗的印度前总理、约旦国王、撒切尔夫人的儿子等等……
                ▲ 伊顿公学
                羁绊深厚的竞争关系，也源于两所名校很多的相似。
                比如，两所学校都有着标志性的校服。伊顿是黑色燕尾服，哈罗是围有深蓝丝带的草帽。
                ▲ 哈罗的标志性草帽
                ▲ 伊顿的黑燕尾服
                两所学校也都非常要求纪律性，对于迟到和着装不规范都有着强硬的惩罚措施，但在体育和活动方面，又都给了男孩们很多的自由。
                ▲ 哈罗公学图书馆
                这次探访，两所名校的招生官也向Kevin介绍了各自的申请流程。
                想把孩子送入伊顿哈罗的家长们该划重点了！

                    伊顿会在英国的5年级（9岁，国内3年级左右）学期末结束接收注册表，每年总共会接收大概1200个学生的注册表进入初选。6年级的第一个学期进行预考，一共考试数学、英语、口语、非口语4门，第一学期结束，圣诞假期前后，预考的结果就会出来。这个阶段会筛选出600个学生左右，进入下一轮考试。
                6年级的第三个学期，将会进行第二轮考试。第二轮考试分两个部分，第一部分学生需要在伊顿参加适应能力的测试，第二部分为面试。招生官特别强调，中国“死记硬背”式的应考模式，在伊顿完全行不通。伊顿最希望看见的，是学生展示出真实的自己，而不是千篇一律的“乖学生”。
                    面试后约一个月出结果，大概会录取225个学生。还有100到150名家庭背景良好、综合实力不错的学生会被放入待录取名单（Waiting 
                List）里，等待被“复活”的可能。今年，就有50名男孩从150名待录取的学生中脱颖而出，收到了“复活”惊喜。
            </div>
            `
        }
    }
}
</script>

