import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import englishEducation from '@/components/english-education'
import activityInfo from '@/components/activity-info'
import activityInfoDetail from '@/components/activity-info-detail'
import schoolSearch from '@/components/school-search'
import schoolSearchResult from '@/components/school-search-result'
import successCase from '@/components/success-case'
import train from '@/components/train'
import schoolDetail from '@/components/school-detail'

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: home
        },
        {
            path: '/english-education',
            component: englishEducation
        },
        {
            path: '/activity-info',
            component: activityInfo
        },
        {
            path: '/activity-info-detail',
            component: activityInfoDetail
        },
        {
            path: '/school-search',
            component: schoolSearch
        },
        {
            path: '/school-search-result',
            component: schoolSearchResult
        },
        {
            path: '/success-case',
            component: successCase
        },
        {
            path: '/train',
            component: train
        },
        {
            path: '/school-detail',
            component: schoolDetail
        }
    ]
})



